// if screen width is less than 1480px
@media screen and (max-width: 1480px) {
  .browse-showcase {
    width: 85% !important;
  }
}
// if screen width is less than 680px
@media screen and (max-width: 680px) {
  .browse {
    flex-direction: column;
    align-items: center;
    margin: 0 !important;

    &-sidebar {
      width: 95% !important;
      max-width: 500px;

      &-filter {
        button {
          margin: 0.15em 0 !important;
        }
      }
    }

    &-showcase {
      margin: 0.5em !important;
      width: 100% !important;

      &-product {
        padding-right: 0.5em !important;
      }
    }
  }
}
// styles for browse section
.browse {
  margin: 0 1em 0 0;
  display: flex;

  &-sidebar {
    width: 10em;
    background-color: $_dark-grey;

    > div h3 {
      margin: -2.31em 0 1em 0;
      text-align: center;
      background-color: rgba(0, 0, 0, 0.6);
      color: white;
      padding: 0.5em 0;
      z-index: 10;
      position: relative;
    }

    img {
      width: 100%;
    }

    &-filter {
      display: flex;
      flex-direction: column;

      & button {
        cursor: pointer;
        text-align: left;
        margin: 0.5em 0;
        padding: 0;
        background-color: $_dark;
        color: $_highlight;
        border: none;

        :hover {
          background-color: $_grey;
        }

        div {
          display: flex;
          align-items: center;
          padding: 0.5em;

          p {
            flex-grow: 1;
            margin: 0;
            padding-left: 1.25em;
          }

          :hover {
            background-color: transparent;
          }
        }
      }

      &-options {
        display: flex;
        flex-direction: column;

        button {
          padding: 0.5em 0.5em 0.5em 2em;
          background-color: $_dark-grey;
          color: rgb(20, 20, 20);
          font-weight: 700;
          margin: 0.15em 0 0.15em 0;
        }

        .active {
          background-color: $_primary !important;
          color: white !important;
        }
      }
    }
  }

  &-showcase {
    display: flex;
    flex-wrap: wrap;
    width: 70%;
    margin: 1em auto;
    justify-content: center;
    align-content: flex-start;

    &-product {
      width: 500px;
      display: flex;
      border: 1px solid $_grey;
      border-radius: 4px;
      margin: 0.25em;
      height: min-content;
      color: $_dark;
      cursor: pointer;

      img {
        width: 100%;
      }

      div:first-of-type {
        width: 50%;
        text-align: center;

        h3 {
          margin: 0.25em 0;
          font-weight: 500;
        }
      }

      div:last-of-type {
        padding-left: 1em;

        p,
        h3 {
          white-space: nowrap;
        }
      }
    }
  }
}
